import { useMutation, useQuery, useQueryClient } from "react-query";
import { useParams } from "react-router";
import { fetchBookingFunction, updateBookingFunction } from "../../api/api";
import { LoadingComponent } from "../reusable_components/loading-content";
import { ErrorAlert } from "../reusable_components/alerts/alerts";
import { Button } from "../reusable_components/custom_button";
import { BookingCard } from "../reusable_components/booking_card";
import { useState } from "react";
import { ThreeDots } from "react-loader-spinner";
import { cn } from "../../utils/custom_functions";

export default function ViewBooking() {
  const [disableUserInterface, setDisableUserInterface] = useState(false);
  const queryClient = useQueryClient();

  const { id } = useParams();

  const {
    data: booking,
    error: bookingErrors,
    isError: isBookingError,
    isLoading: isBookingLoading,
    // isSuccess: isBookingSuccess,
  } = useQuery({
    queryKey: ["fetched-booking"],
    queryFn: () => fetchBookingFunction(id),
    onSuccess: (data) => {
      console.log(data);
    },
  });

  const {
    isLoading: isCancelLoading,
    isSuccess: isCancelSucces,
    mutateAsync: cancelBookingAsync,
    isError: isCancelError,
    error: cancelError,
  } = useMutation("cancel-booking", {
    mutationFn: updateBookingFunction,
    onSuccess: (event) => {
      setDisableUserInterface((s) => false);
      queryClient.invalidateQueries(["fetched-booking"]);
    },
    onMutate: (event) => {
      setDisableUserInterface((l) => true);
    },
    onError: (event) => {
      setDisableUserInterface((e) => false);
    },
  });

  const handleCancelation = async () => {
    try {
      await cancelBookingAsync({ booking_canceled: true, id: booking.id });
    } catch (error) {}
  };

  if (isBookingLoading) {
    return <LoadingComponent secondary_text={"loading booking"} />;
  }

  if (isBookingError) {
    return <ErrorAlert secondaryText={`${bookingErrors.response.data}`} />;
  }

  return (
    <main className="max-w-xl mx-auto p-6 my-6 text-black bg-white">
      <div className="rounded-lg p-6 shadow-sm border border-gray-200">
        <h2
          className={cn("text-start text-black px-2 font-extrabold text-3xl", {
            "text-red-500": booking.booking_canceled,
            "text-gray-200": booking.booking_completed,
          })}
        >
          Booking {id}
          <p className="text-sm font-normal text-slate-600 pt-2">
            Thank you for trusting Matols Logistic Services with your personal
            information.
          </p>
        </h2>
      </div>
      <div className="my-4" />
      <BookingCard key={booking.id} booking={booking}>
        <div>
          {isCancelError && (
            <ErrorAlert
              className="mb-4"
              secondaryText={`${cancelError.response.data.message} ${cancelError.message}`}
            />
          )}

          <div className="flex flex-row justify-end gap-4 mt-4">
            <Button
              onClick={handleCancelation}
              disabled={
                booking.booking_canceled ||
                booking.booking_completed ||
                disableUserInterface
              }
              className="w-auto bg-red-500 text-white"
            >
              {isCancelLoading ? (
                <div className="w-full h-full flex justify-center items-center">
                  <ThreeDots visible width="40" height="20" color="#000" />
                </div>
              ) : (
                "Cancel"
              )}
            </Button>
            <Button
              onClick={() => {
                window.location.href = `/update/booking/${booking.id}/`;
              }}
              disabled={
                booking.booking_canceled ||
                booking.booking_completed ||
                disableUserInterface
              }
              className="w-auto"
            >
              Update
            </Button>
          </div>
        </div>
      </BookingCard>
    </main>
  );
}
